
// Pointing arrows
// ===================================

.lui.pointing:after {
	content: '';
	display: block;
	width: 0;
	height: 0;
	border-style: solid;
	border-color: transparent;
}
.lui[class*="top pointing"]:after,
.lui[class*="bottom pointing"]:after {
	border-left-width: $triangle-width;
	border-right-width: $triangle-width;
}
.lui[class*="top pointing"]:after {
	border-bottom: $triangle-width solid $triangle-color;
}
.lui[class*="bottom pointing"]:after {
	border-top: $triangle-width solid $triangle-color;
}
.lui[class*="left pointing"]:after,
.lui[class*="right pointing"]:after {
	border-top-width: $triangle-width;
	border-bottom-width: $triangle-width;
}
.lui[class*="left pointing"]:after {
	border-right: $triangle-width solid $triangle-color;
}
.lui[class*="right pointing"]:after {
	border-left: $triangle-width solid $triangle-color;
}
